<template>
  <div class="header">
    <!-- 自适应 x<1200 -->
    <el-row class="hidden-lg-and-up" :gutter="0">
      <el-col :span="1">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="2">
        <el-button class="optionBtn" @click="setOption()">〓</el-button>
        <div class="option">
          <span @click="tabChoose(1,'small')" class="optionChild">{{$t("Header.about_us")}}</span>
          <span @click="tabChoose(2,'small')" class="optionChild" href>{{$t("Header.knowledge")}}</span>
          <span @click="tabChoose(3,'small')" class="optionChild" href>{{$t("Header.search")}}</span>
          <span @click="tabChoose(4,'small')" class="optionChild" href>{{$t("Header.employ")}}</span>
          <span @click="tabChoose(5,'small')" class="optionChild" href>{{$t("Header.star")}}</span>
          <a class="optionChild LAN_sm">
            <span>
              <el-button @click="langChoose('zh')" :style="{color:style.Color_Chinese}">中文</el-button>
            </span>
            <span>|</span>
            <span>
              <el-button @click="langChoose('en')" :style="{color:style.Color_English}">English</el-button>
            </span>
          </a>
        </div>
      </el-col>
      <el-col :span="6">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="6">
        <span class="logo">
          <el-button @click="tabChoose(0)">
            <el-image :src="style.src_logo" ></el-image>
          </el-button>
        </span>
      </el-col>
      <el-col :span="6">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="2">
        <el-button class="searchBtn" @click="setSearch()" icon="el-icon-search"></el-button>
      </el-col>
      <el-col :span="1">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <div class="search">
      <span class="searchChild"></span>
      <el-autocomplete
        class="searchChild"
        popper-class="my-autocomplete"
        v-model="state"
        :fetch-suggestions="querySearch"
        :placeholder="$t('Header.Please_enter_content')"
        @select="handleSelect"
      >
        <i class="el-icon-edit el-input__icon" slot="suffix" @click="handleIconClick()"></i>
        <template slot-scope="{ item }">
          <div @click="goSearchChoose(item.productId)">
            <!-- <div> -->
            <div v-show="false" class="name">{{ item.value }}</div>
            <div class="name">{{ item.productName }}</div>
            <div class="addr">{{ item.market }}</div>
          </div>
        </template>
      </el-autocomplete>
      <span class="searchChild"></span>
      <span class="searchChild"></span>
      <span class="searchChild"></span>
    </div>

    <!-- 自适应 1200<x -->
    <el-row class="hidden-md-and-down" :gutter="0">
      <el-col :span="11">
        <span class="tabs">
          <el-dropdown>
            <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
            <el-button
              id="tab1"
              @click="tabChoose(1,'large')"
              :style="{color:style.Color_tab1,}"
            >{{$t("Header.about_us")}}</el-button>
            <!-- <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>{{$t("Header.industry_application_item[0]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[1]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[2]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[3]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[4]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[5]")}}</el-dropdown-item>
              <el-dropdown-item>{{$t("Header.industry_application_item[6]")}}</el-dropdown-item>
            </el-dropdown-menu>-->
          </el-dropdown>
          <el-button
            id="tab2"
            @click="tabChoose(2,'large')"
            :style="{color:style.Color_tab2}"
          >{{$t("Header.knowledge")}}</el-button>
          <el-button
            id="tab3"
            @click="tabChoose(3,'large')"
            :style="{color:style.Color_tab3}"
          >{{$t("Header.search")}}</el-button>
          <el-button
            id="tab4"
            @click="tabChoose(4,'large')"
            :style="{color:style.Color_tab4}"
          >{{$t("Header.employ")}}</el-button>
          <el-button
            id="tab5"
            @click="tabChoose(5,'large')"
            :style="{color:style.Color_tab5}"
          >{{$t("Header.star")}}</el-button>
        </span>
      </el-col>
      <el-col :span="3">
        <span class="logo">
          <el-button @click="tabChoose(0,'none')">
            <el-image :src="style.src_logo" ></el-image>
          </el-button>
        </span>
      </el-col>
      <el-col :span="5">
        <p>&nbsp;</p>
      </el-col>
      <el-col class="header_right" :span="5">
        <el-button class="searchBtn" @click="setSearch()" icon="el-icon-search"></el-button>
        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <!-- <span>客服电话：400-1234567</span> -->
        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <span class="LAN">
          <span>
            <el-button @click="langChoose('zh')" :style="{color:style.Color_Chinese}">中文</el-button>
          </span>
          <span>|</span>
          <span>
            <el-button @click="langChoose('en')" :style="{color:style.Color_English}">English</el-button>
          </span>
        </span>
        <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
        <span>
          <!-- <el-button class="login">登录</el-button> -->
        </span>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Vue from "vue";
import "@/styles/header.css";
import { fail } from "assert";
import { mapGetters } from "vuex";
import main from "@/main";

export default {
  name: "Header",
  computed: {
    ...mapGetters(["machine"])
  },
  components: {},
  data() {
    return {
      keyword: null,
      style: {
        // src_logo: require("@/assets/logo.png"),
        src_logo: require("@/assets/maid/logo.png"),
        Color_Chinese: "#3D68C6",
        Color_English: "#333333",
        Color_tab1: "#333333",
        Color_tab2: "#333333",
        Color_tab3: "#333333",
        Color_tab4: "#333333",
        Color_tab5: "#333333",
      },
      optionStatus: false,
      setOptionDoor: true, //防快速点击
      searchStatus: false,
      setSearchDoor: true, //防快速点击
      searchInfo: null,
      // 自动补全search内容
      restaurants: [],
      state: "",
      machineManaged: [],
      // MyQuery: null
      MyQuery: {
        searchType: "none",
        productId: "none",
        searchText: "none"
      }
    };
  },
  created() {
    let vm = this;
    setTimeout(() => {
      vm.set_machineManaged(vm.machine);
    }, 500);
  },
  mounted() {
    // 自动补全search内容
    setTimeout(() => {
      this.restaurants = this.loadAll();
    }, 700);
  },
  updated() {
    let vm = this;
    setTimeout(() => {
      vm.set_machineManaged(vm.machine);
    }, 600);
    setTimeout(() => {
      this.restaurants = this.loadAll();
    }, 800);
  },
  methods: {
    langChoose(str_lang) {
      let vm = this;
      let data = {
        messages: main.messages,
        str_lang: str_lang
      };
      // 刷新store-lang
      vm.$store
        .dispatch("set_lang", data)
        .then(function(response) {
          // console.log("header - set_lang");
          // console.log("str_lang:" + str_lang);
          // 刷新store-machine
          vm.$store
            .dispatch("update_machine", str_lang)
            .then(function(response) {
              // console.log("header - update_machine");
              // console.log("str_lang:" + str_lang);
              ///////// 刷新搜索框的内容 store-dataSearch
              let clickType = "lang";
              vm.searchSetting(vm, vm.MyQuery, vm.machine, clickType);
              /////////

              if (str_lang == "zh") {
                vm.style.Color_Chinese = "#3D68C6";
                vm.style.Color_English = "#333333";
              } else if (str_lang == "en") {
                vm.style.Color_Chinese = "#333333";
                vm.style.Color_English = "#3D68C6";
              }
            })
            .catch(function(error) {
              console.info(error);
            });

          // vm.$router.go(0); //刷新当前页面
        })
        .catch(function(error) {
          console.info(error);
        });
    },
    set_pageId(pageId) {
      let vm = this;
      vm.$store
        .dispatch("set_pageId", pageId)
        .then(function(response) {
          // console.log(response);
        })
        .catch(function(error) {
          console.info(error);
        });
    },
    tabChoose(ordinal, BtnType) {
      // BtnType定义： small - 收缩选项按钮 ， large - 正常展开按钮 ， none - logo处无关
      let vm = this;
      vm.initTabsColor();
      let pageId;
      switch (ordinal) {
        case 0:
          pageId = 0;
          vm.$router.push({ name: "FilipinoMaid" });
          break;
        case 1:
          document.getElementById("tab1").style.color = "#588FEF";
          pageId = 1;
          vm.$router.push({ name: "AboutUs" });
          break;
        case 2:
          document.getElementById("tab2").style.color = "#588FEF";
          pageId = 2;
          vm.$router.push({ name: "ProductModel" });
          break;
        case 3:
          document.getElementById("tab3").style.color = "#588FEF";
          pageId = 3;
          vm.$router.push({ name: "SearchMaid" });
          break;
        case 4:
          document.getElementById("tab4").style.color = "#588FEF";
          pageId = 4;
          vm.$router.push({ name: "EmployMaid" });
          break;
        case 5:
          document.getElementById("tab5").style.color = "#588FEF";
          pageId = 5;
          vm.$router.push({ name: "StarMaid" });
          break;
        default:
      }
      // 自动关闭下拉选项
      if (BtnType == "small") {
        if (vm.searchStatus == true) {
          vm.Search();
          setTimeout(() => {
            vm.Option();
          }, 800);
        } else {
          vm.Option();
        }
      }
      vm.set_pageId(pageId);
    },
    initTabsColor() {
      document.getElementById("tab1").style.color = "#333333";
      // document.getElementById("tab2").style.color = "#333333";
      document.getElementById("tab3").style.color = "#333333";
      document.getElementById("tab4").style.color = "#333333";
      document.getElementById("tab5").style.color = "#333333";
    },
    setOption() {
      let vm = this;
      if (vm.searchStatus == true) {
        // 开启option前,如果search是开启状态，则需要先关掉search
        vm.Search();
        setTimeout(() => {
          vm.Option();
        }, 800);
      } else {
        // 否则直接开启option
        vm.Option();
      }
    },
    Option() {
      let vm = this;
      if (vm.setOptionDoor == true) {
        vm.setOptionDoor = false;
        vm.optionStatus = !vm.optionStatus;
        // 按钮样式变化
        let ele_optionBtn = document.getElementsByClassName("optionBtn");
        let current = 0;
        if (vm.optionStatus == true) {
          current = (current + 180) % 360;
          ele_optionBtn[0].style.transform = "rotate(" + current + "deg)";
          ele_optionBtn[0].style.transition = "all 0.8s ease-in 0s";
          setTimeout(() => {
            ele_optionBtn[0].innerHTML = "Ⅹ";
          }, 700);
        } else {
          current = (current - 180) % 360;
          ele_optionBtn[0].style.transform = "rotate(" + current + "deg)";
          ele_optionBtn[0].style.transition = "all 0.8s ease-in 0s";
          setTimeout(() => {
            ele_optionBtn[0].innerHTML = "〓";
          }, 700);
        }
        let ele_option = document.getElementsByClassName("option");
        let ele_optionChild = document.getElementsByClassName("optionChild");
        let length = ele_optionChild.length;
        let i;
        let count = 0;
        if (vm.optionStatus == true) {
          ele_option[0].style.display = "inherit";
          setTimeout(() => {
            // ele_option[0].style.height = "370px";
            ele_option[0].style.height = "45vw";
            ele_option[0].style.transition = "all 0.8s ease-in 0s";
            setChildShowStatus(setChildShowStatus, true);
          }, 50);
        } else {
          ele_option[0].style.height = "0px";
          ele_option[0].style.transition = "all 0.8s ease-in 0s";
          setChildShowStatus(setChildShowStatus, false);
        }
        function setChildShowStatus(callback, status) {
          if (count < length) {
            if (status == true) {
              setTimeout(() => {
                // ele_optionChild[count].style.fontSize = "3vw";
                ele_optionChild[count].style.display = "block";
                count++;
                callback(setChildShowStatus, status);
              }, 165);
            } else {
              setTimeout(() => {
                ele_optionChild[length - 1 - count].style.display = "none";
                count++;
                callback(setChildShowStatus, status);
              }, 140);
            }
          } else {
            // console.log("回调结束");
            if (status == false) {
              ele_option[0].style.display = "none";
            }
            vm.setOptionDoor = true;
          }
        }
      }
    },
    setSearch() {
      let vm = this;
      if (vm.optionStatus == true) {
        vm.Option();
        setTimeout(() => {
          vm.Search();
        }, 800);
      } else {
        vm.Search();
      }
    },
    Search() {
      // 清空输入框内容
      this.state = "";
      let vm = this;
      if (vm.setSearchDoor == true) {
        vm.setSearchDoor = false;
        vm.searchStatus = !vm.searchStatus;
        // 按钮样式变化
        let ele_searchBtn = document.getElementsByClassName("searchBtn");
        let current = 0;
        if (vm.searchStatus == true) {
          current = (current + 359) % 360;
          ele_searchBtn[0].style.transform = "rotate(" + current + "deg)";
          ele_searchBtn[0].style.transition = "all 0.8s ease-in 0s";
          ele_searchBtn[1].style.transform = "rotate(" + current + "deg)";
          ele_searchBtn[1].style.transition = "all 0.8s ease-in 0s";
        } else {
          current = (current - 359) % 360;
          ele_searchBtn[0].style.transform = "rotate(" + current + "deg)";
          ele_searchBtn[0].style.transition = "all 0.8s ease-in 0s";
          ele_searchBtn[1].style.transform = "rotate(" + current + "deg)";
          ele_searchBtn[1].style.transition = "all 0.8s ease-in 0s";
        }
        let ele_search = document.getElementsByClassName("search");
        let ele_searchChild = document.getElementsByClassName("searchChild");
        let length = ele_searchChild.length;
        let i;
        let count = 0;
        if (vm.searchStatus == true) {
          ele_search[0].style.display = "inherit";
          setTimeout(() => {
            // ele_search[0].style.height = "370px";
            ele_search[0].style.height = "45vw";
            ele_search[0].style.transition = "all 0.8s ease-in 0s";
            setChildShowStatus(setChildShowStatus, true);
          }, 50);
        } else {
          ele_search[0].style.height = "0px";
          ele_search[0].style.transition = "all 0.8s ease-in 0s";
          setChildShowStatus(setChildShowStatus, false);
        }
        function setChildShowStatus(callback, status) {
          if (count < length) {
            if (status == true) {
              setTimeout(() => {
                ele_searchChild[count].style.display = "block";
                count++;
                callback(setChildShowStatus, status);
              }, 165);
            } else {
              setTimeout(() => {
                ele_searchChild[length - 1 - count].style.display = "none";
                count++;
                callback(setChildShowStatus, status);
              }, 140);
            }
          } else {
            // console.log("回调结束");
            if (status == false) {
              ele_search[0].style.display = "none";
            }
            vm.setSearchDoor = true;
          }
        }
      }
    },
    // 自动补全search内容(头)
    querySearch(queryString, cb) {
      var restaurants = this.restaurants;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          // restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          // 0
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) !=
          -1
        );
      };
    },
    set_machineManaged(machine) {
      // console.log("set_machineManaged");

      // 优化- 补全框显示（包括所有内容的）value
      let length = machine.length;
      let i;
      let temp;
      let tempMachine = machine;
      this.machineManaged = new Array(); //初始化machineManaged - 防止数组叠加
      for (i = 0; i < length; i++) {
        temp = JSON.stringify(machine[i]);
        tempMachine[i].value = temp;
        this.machineManaged.push(tempMachine[i]);
      }
    },
    loadAll() {
      // console.log("loadAll");
      // console.log(this.machineManaged);
      return this.machineManaged;
    },
    handleSelect(item) {},
    handleIconClick(ev) {
      let vm = this;
      let query = {
        searchType: "search",
        productId: "none",
        searchText: vm.state
      };
      vm.MyQuery = null; // 初始化
      vm.MyQuery = query; //组件-全局变量（目的切换lang能刷新set_dataSearch）
      let clickType = "search";
      vm.searchSetting(vm, query, vm.machine, clickType);
    },
    // 自动补全search内容(尾)
    goSearchChoose(productId) {
      let vm = this;
      let query = {
        searchType: "choose",
        productId: productId,
        searchText: vm.state
      };
      vm.MyQuery = null; // 初始化
      vm.MyQuery = query; //组件-全局变量（目的切换lang能刷新set_dataSearch）
      let clickType = "search";
      vm.searchSetting(vm, query, vm.machine, clickType);
    },
    searchSetting(vm, query, machine, clickType) {
      vm.$store
        .dispatch("set_searchQuery", query)
        .then(function(response) {
          vm.refreshData(query, machine)
            .then(function(res) {
              // console.log(response);
              // 关闭搜索框
              if (clickType != "lang") {
                // 修复lang导致弹出下拉框
                vm.setSearch();
                // 跳转搜索结果
                vm.$router.push({
                  name: "Search"
                });
              }
              // 初始化
              vm.state = "";
              // 关闭补全面板
              let completeMenu = document.getElementsByClassName(
                "my-autocomplete"
              );
              completeMenu[0].style.display = "none";
            })
            .catch(function(error) {
              console.info(error);
            });
        })
        .catch(function(error) {
          console.info(error);
        });
    },
    refreshData(query, machine) {
      let vm = this;
      return new Promise((resolve, reject) => {
        // 处理显示零件-by搜索框的值
        vm.dataManager(
          machine,
          query.searchType,
          query.productId,
          query.searchText
        )
          .then(function(res) {
            resolve("refreshData - SUCCESS");
          })
          .catch(function(error) {
            console.info(error);
          });
      });
    },
    dataManager(dataList, searchType, productId, searchText) {
      return new Promise((resolve, reject) => {
        let vm = this;
        let i;
        let dataListLength = dataList.length;
        let tem_dataSearch = [];
        let result1;
        let str;
        let str_productId;
        if (searchType == "search") {
          for (i = 0; i < dataListLength; i++) {
            str = JSON.stringify(dataList[i]);
            result1 = str.indexOf(searchText);
            if (result1 != -1) {
              tem_dataSearch.push(dataList[i]);
            }
          }
        } else if (searchType == "choose") {
          for (i = 0; i < dataListLength; i++) {
            str = JSON.stringify(dataList[i]);
            str_productId = '"productId":"' + String(productId) + '"';
            result1 = str.indexOf(str_productId);
            if (result1 != -1) {
              tem_dataSearch.push(dataList[i]);
            }
          }
        }
        vm.$store
          .dispatch("set_dataSearch", tem_dataSearch)
          .then(function(resX) {
            resolve("dataManager - SUCCESS");
          })
          .catch(function(error) {
            console.info(error);
          });
      });
    }
  }
};
</script>

<style scoped>
</style>

<style>
.header {
  margin-top: 20px;
  margin-bottom: 5px;
}

.header .logo {
  height: 33px;
}
.header .logo .el-image {
}
.header .logo .el-image__inner {
}

.header .option {
  display: none;
  position: absolute;
  left: 0;
  width: 100%;
  height: 0px;
  padding: 10px 50px;
  box-sizing: border-box;
  transition: all 0.5s 0.5s;

  background-color: #588fef;
  filter: alpha(opacity=90);
  -moz-opacity: 0.9;
  -khtml-opacity: 0.9;
  opacity: 0.9;
  text-align: center;

  z-index: 1000;
}
.header .option > span,
.header .option > a {
  display: none;
  color: white;
  text-decoration: none;
  margin-top: 10px;
  padding-bottom: 1vw;
  border-bottom: 1px solid rgb(13, 102, 218);
  font-size: 2vw;
}
.header .option > a:hover {
  color: rgb(13, 102, 218);
}

.header .search {
  display: none;
  position: absolute;
  left: 0;
  width: 100%;
  height: 0px;
  padding: 10px 50px;
  box-sizing: border-box;
  transition: all 0.5s 0.5s;

  background-color: #588fef;
  filter: alpha(opacity=90);
  -moz-opacity: 0.9;
  -khtml-opacity: 0.9;
  opacity: 0.9;
  text-align: center;
  z-index: 1000;
}

.header .search > span {
  display: none;
  color: white;
  text-decoration: none;
  margin-top: 10px;
  padding-bottom: 10px;
  font-size: 14px;
}
.header .el-autocomplete {
  display: inline;
}
.header .el-input__inner {
  height: 46px;
  width: 100%;
}
.header .input {
}

.header .my-autocomplete li {
  line-height: normal;
  padding: 7px;
}

.header .my-autocomplete li .name {
  text-overflow: ellipsis;
  overflow: hidden;
}
.header .my-autocomplete li .addr {
  font-size: 12px;
  color: #b4b4b4;
}
.header .my-autocomplete li .highlighted .addr {
  color: #ddd;
}
.header .header_right {
  margin-top: 35px;
}
.header .LAN {
  white-space: nowrap; /*禁止转行*/
}
.header .LAN span {
  font-size: 12px;
}
.header .LAN .el-button {
  padding: 0px 0px;
}

.header .LAN_sm {
  font-size: 12px !important;
  padding-top: 10px !important;
  padding-bottom: 15px !important;
}
.header .LAN_sm .el-button {
  padding: 0px 0px;
  background-color: rgba(0, 0, 0, 0);
}

.header .tabs {
  white-space: nowrap; /*禁止转行*/
}
.header .el-button {
  padding: 0px 10px;
  /* font-size: 24px; */
  font-size: 16px;
  border: 0px;
}

.header #tab1:hover + #board {
  display: block;
}
.header #board p {
  margin: 0 0;
  height: 44px;
}
.header #board button {
  font-size: 26px;
  background-color: #78a4f0;
  color: #ffffff;
}

.header .tabs > a:hover {
}

.header .el-button {
}

.header .login {
  font-size: 10px;
}
</style>



