<template>
  <div class="view filipinomaid common">
    <el-image :style="{width:'100%'}" :src="style.src_pic"></el-image>
    <!-- <el-row class="title" :gutter="0">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col :span="18">
        <div>
          <p class="branches_title">&nbsp;&nbsp;{{$t("AboutUs.company_introduction")}}</p>
        </div>
      </el-col>
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>-->

    <el-row class="cooperator" :gutter="0">
      <el-col :span="3">
        <p>&nbsp;</p>
      </el-col>
      <el-col class="intro" :span="7">
        <p class="number">10万+</p>
        <p class="object">菲佣数</p>
      </el-col>
      <el-col class="intro borderM" :span="4">
        <p class="number">1000+</p>
        <p class="object">合作方</p>
      </el-col>
      <el-col class="intro" :span="7">
        <p class="number">10000+</p>
        <p class="object">雇主数</p>
      </el-col>
      <el-col class="intro" :span="3">
        <p>&nbsp;</p>
      </el-col>
    </el-row>
    <index-module :style="ListBackground"></index-module>
    <!-- <div class="body_bottom color_bg"></div> -->
  </div>
</template>

<script>
import "@/styles/filipinomaid.css";
import "@/styles/common.css";

import IndexModule from "./components/IndexModule"; //新版首页行业应用

export default {
  name: "FilipinoMaid",
  components: {
    "index-module": IndexModule
  },

  data() {
    return {
      style: {
        src_pic: require("@/assets/maid/pic (1).png"),
        src_intro: require("@/assets/introduce1.png"),
        src_map: require("@/assets/introduce1.png")
      },
      ListBackground: {
        backgroundImage: "url(" + require("@/assets/maid/bg.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100%"
      }
    };
  },
  mounted() {},
  methods: {}
};
</script>

<style scoped>
</style>

<style>
.filipinomaid {
  background-color: #ffffff;
}
.filipinomaid .branches_content {
  color: #333333;
  white-space: pre-line;
  margin-bottom: -20px;
}

.filipinomaid .company_intro {
  text-align: center;
}
.filipinomaid .company_intro > p {
  text-align: justify;
}

.filipinomaid .company_intro .el-image {
  text-align: center;
}

.filipinomaid .contact_information {
  margin-top: 24px;
}
.filipinomaid .contact_information_img {
  margin-top: 24px;
}

.filipinomaid .contact_information_img .el-image {
}

.filipinomaid .contact_information > div > p {
  font-size: 24px;
  font-weight: bold;
}
.filipinomaid .contact_information > p {
}
.filipinomaid .contact_information .child1 {
}
.filipinomaid .contact_information .child2 {
  margin-top: 2.34vw;
}
.filipinomaid .body_bottom {
}

.filipinomaid .cooperator {
  height: 120px;
  background-color: #ffffff;
}
.filipinomaid .cooperator:nth-child(n) {
  /* margin: auto 0px; */
  padding-top: 30px;
}
.filipinomaid .cooperator .intro {
  text-align: center;
}
.filipinomaid .cooperator .borderM {
  border-left: 2px solid #c6c6c6 !important;
  border-right: 2px solid #c6c6c6 !important;
}
.filipinomaid .cooperator .number {
  color: #2461a2;
  font-size: 28px;
  margin: 0 auto;
}
.filipinomaid .cooperator .object {
  color: #333333;
  font-size: 20px;
  margin: 0 auto;
}
</style>



