<template>
  <div class="module">
    <div class="main_module_body" v-show="currentLang == 'zh'">
      <el-row :style="ListBackground" :gutter="0">
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
        <el-col class="modulelist" :span="20">
          <div
            class="module_content"
            v-for="(item,index) in module.slice(0,3)"
            :key="index + 'aasda' "
            @click="ShowDetail()"
          >
            <div class="avatar">
              <img :style="{width:'50%',height:'50%'}" :src="item.avatarSmall">
            </div>
            <div class="module_product_name">{{item.Name}}</div>
            <div class="module_info">
              <el-row :gutter="0">
                <el-col :span="11">
                  <p>性别：{{item.Sex}}</p>
                  <p>婚否：{{item.MaritalStatus}}</p>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="11">
                  <p>年龄：{{item.Age}}</p>
                  <p>经验：{{item.WorkExp}}</p>
                </el-col>
              </el-row>
              <p>国籍：{{item.Nationality}}</p>
              <p>
                海外工作经验：
                <span
                  v-for="(itemChilren,index) in item.OverseasWorkplace"
                  :key="index + 'c' "
                >
                  <span>{{itemChilren}}&nbsp;&nbsp;</span>
                </span>
              </p>
              <p>
                女佣星级：
                <span
                  v-for="(item3Children,index) in item.TheMaidStar"
                  :key="index + 'aasda' "
                >
                  <span class="level">★</span>
                </span>
              </p>
            </div>
          </div>

          <!--  -->
          <div
            class="module_content"
            v-for="(item2,index2) in module.slice(3,6)"
            :key="index2 + 'aasda2' "
          >
            <div class="avatar">
              <img :style="{width:'50%',height:'50%'}" :src="item2.avatarSmall">
            </div>
            <div class="module_product_name">{{item2.Name}}</div>
            <div class="module_info">
              <el-row :gutter="0">
                <el-col :span="11">
                  <p>性别：{{item2.Sex}}</p>
                  <p>婚否：{{item2.MaritalStatus}}</p>
                </el-col>
                <el-col :span="2">
                  <p>&nbsp;</p>
                  <p>&nbsp;</p>
                </el-col>
                <el-col :span="11">
                  <p>年龄：{{item2.Age}}</p>
                  <p>经验：{{item2.WorkExp}}</p>
                </el-col>
              </el-row>
              <p>国籍：{{item2.Nationality}}</p>
              <p>
                海外工作经验：
                <span
                  v-for="(item2Chilren,index) in item2.OverseasWorkplace"
                  :key="index + 'c' "
                >
                  <span>{{item2Chilren}}&nbsp;&nbsp;</span>
                </span>
              </p>
              <p>
                女佣星级：
                <span
                  v-for="(item3Children,index) in item2.TheMaidStar"
                  :key="index + 'aasda' "
                >
                  <span class="level">★</span>
                </span>
              </p>
            </div>
          </div>
        </el-col>
        <el-col :span="2">
          <p>&nbsp;</p>
        </el-col>
      </el-row>
      <div class="set_bottom">
        <p v-show="currentLang == 'zh'">更多 >></p>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  name: "IndexModule",
  computed: {
    ...mapGetters(["currentLang", "module"])
  },
  data() {
    return {
      ListBackground: {
        backgroundImage: "url(" + require("@/assets/newpic11.png") + ")",
        backgroundRepeat: "no-repeat",
        backgroundSize: "100%"
      }
    };
  },
  methods: {
    ShowDetail() {
      console.log("~~~~ShowDetail")
      let vm = this;
      vm.$store
        .dispatch("update_infoIsShow", 1, true)
        .then(function (response) {
        })
        .catch(function (error) {
          console.info(error);
        });
    }
  }
};
</script>
<style scoped>
.module {
  width: 100%;
  max-width: 2000px;
  /* min-width: 1200px; */
  background: #f9f9f9;
  padding: 60px 0px;
}
.module .main_module_body {
  /* width: 1440px; */
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 20px;
}
.module .module_content {
  width: 25%;
  /* margin: 50px 40px 0px 50px; */
  margin-top: 50px;
  margin-left: 40px;
  margin-right: 40px;
  margin-bottom: 50px;
  float: left;
  background: #fff;
}

.module .module_product_name {
  text-align: center;
  font-size: 16px;
  margin: 10px 0px;
}

/* 自适应开始 */
@media screen and (min-width: 1920px) {
  .module .main_module_body {
    /* width: 1440px; */
  }
  .module .module_content_main {
    height: 250px;
  }
}
@media screen and (max-width: 1919px) {
  .module .main_module_body {
    /* width: 1440px; */
  }
  .module .module_content_main {
    height: 250px;
  }
}
@media screen and (max-width: 1600px) {
  .module .main_module_body {
    /* width: 1250px; */
  }
  .module .module_content_main {
    height: 200px;
  }
}
@media screen and (max-width: 1400px) {
  .module .main_module_body {
    /* width: 1000px; */
  }
  .module .module_content_main img {
    width: 90%;
  }
  .module .module_content_main {
    height: 150px;
  }
}

/* ----new */

.module .module_content .avatar {
  text-align: center;
  /* width: 50px;
  height: 50px;
  border-radius: 50px;
  -moz-border-radius: 50px; */
  padding-top: 20px;
}

.module .module_content .avatar > img {
  /* width: 50px;
  height: 50px;
  border-radius: 50px;
  -moz-border-radius: 50px;
  overflow: hidden; */
}
.module .module_info {
  margin-left: 25px;
}

.module .set_bottom {
  height: 40px;
  text-align: right;
}

.module .set_bottom > p {
  color: #068ffc;
  font-size: 18px;
  margin-top: 60px;
  margin-right: 220px;
}

.module .level {
  font-size: 20px;
  color: #ffc618;
  display: inline-block;
}
</style>





