<template>
  <div class="message_board">
    <div class="message_content">
      <div class="close_icon" @click="controlIsshow">
        <i class="el-icon-circle-close"></i>
      </div>
      <div class="show_messags">
        <p v-show="!showError">{{$t("MessageBoard.Please_fill_in_the_eoi_form")}}</p>
        <div
          class="show_error"
          v-text="message"
          v-show="showError"
          :style=" 'background-color:' + backgroundColor +';' + 'color:' + fontColor"
        ></div>
      </div>
      <div class="message_text">
        <div class="set_message">
          <p>
            {{$t("MessageBoard.name")}}
            <i>*</i>
          </p>
          <p>
            <input type="text" v-model="name">
          </p>
        </div>
        <div class="set_message">
          <p>
            {{$t("MessageBoard.phone")}}
            <i>*</i>
          </p>
          <p>
            <input type="text" v-model="phone">
          </p>
        </div>
      </div>
      <div class="message_text">
        <div class="set_message">
          <p>
            {{$t("MessageBoard.mail")}}
            <i>*</i>
          </p>
          <p>
            <input type="text" v-model="email">
          </p>
        </div>
        <div class="set_message">
          <p>{{$t("MessageBoard.address")}}</p>
          <p>
            <input type="text" v-model="address">
          </p>
        </div>
      </div>
      <div class="message_textarea">
        <P>{{$t("MessageBoard.content")}}</P>
        <p>
          <textarea name id cols="30" rows="10" v-model="text"></textarea>
        </p>
      </div>
      <div
        class="submit_btn"
        @click="submitMessage"
      >{{$t("MessageBoard.Submit_an_expression_of_interest")}}</div>
    </div>
  </div>
</template>
<script>
export default {
  name: "MessageBoard",
  data() {
    return {
      name: null,
      phone: null,
      email: null,
      address: null,
      text: null,
      showError: false,
      backgroundColor: "",
      fontColor: " ",
      message: " ",
    };
  },
  methods: {
    setIsshow(message, bgColor, fontColor) {
      this.showError = true
      this.message = message
      this.backgroundColor = bgColor
      this.fontColor = fontColor
      setTimeout(() => {
        this.showError = false
      }, 2000)
    },
    submitMessage() {
      let vm = this;
      let postData = {};
      postData.name = vm.name;
      postData.mobile = vm.phone;
      postData.email = vm.email;
      postData.address = vm.address;
      postData.content = vm.text;

      if (vm.name == null) {
        vm.setIsshow(vm.$t("MessageBoard.hint_message[0]"), "#fdf6ec", "#e6a23c")
        return false;
      } else if (!/^1[34578]\d{9}$/.test(vm.phone)) {
        vm.setIsshow(vm.$t("MessageBoard.hint_message[1]"), "#fdf6ec", "#e6a23c")
        return false;
      } else if (vm.email == null) {
        vm.setIsshow(vm.$t("MessageBoard.hint_message[2]"), "#fdf6ec", "#e6a23c")
        return false;
      } else {
        vm.setIsshow()
        vm.$http({
          method: "post",
          url: "http://sitemap.companycheck.cn/wt/public/index.php",
          data: postData
        })
          .then(function (response) {
            if (response.errcode == 1) {
              vm.setIsshow(vm.$t("MessageBoard.hint_message[3]"), "#fdf6ec", "#e6a23c")
            } else if (response.errcode == 0) {
              vm.setIsshow(vm.$t("MessageBoard.hint_message[4]"), "#f0f9eb", "#67c23a ")
              vm.name = null
              vm.phone = null
              vm.email = null
              vm.address = null
              vm.text = null
            }
          })
          .catch(function (error) {
            vm.setIsshow(vm.$t("MessageBoard.hint_message[5]"), "#fef0f0", "#f56c6c")
            console.info(error);
          });
      }
    },
    controlIsshow() {
      this.$emit("controlIsshow", "false")
      this.name = null
      this.phone = null
      this.email = null
      this.address = null
      this.text = null
    }
  }
}
</script>
<style scoped>
.message_board {

}
.message_board .close_icon {
  font-size: 22px;
  color: #666;
  position: absolute;
  top: -3px;
  right: 3px;
  cursor: pointer;
}
.message_board .close_icon:hover {
  color: #305dc3;
}
.message_board .message_content {
  background-color: #fbfbfb;
  z-index: 2400 !important;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  padding: 20px 0px 20px 20px;
  border: 1px solid #ccc;
  border-radius: 2px;
  box-shadow: 0px 0px 10px #ccc;
}
.message_board .show_messags {
  height: 40px;
  line-height: 40px;
}
.message_board .show_messags p {
  margin-left: 10px;
  font-size: 16px;
  width: 94%;
  color: #406fce;
  background: #a3c1ff;
  border-radius: 2px;
  padding-left: 10px;
}
.message_content .show_error {
  margin-left: 10px;
  width: 94%;
  line-height: 40px;
  font-size: 16px;
  padding-left: 10px;
  border-radius: 2px;
  margin-bottom: 7px;
}
.message_content .message_text {
  width: 100%;
  overflow: hidden;
}
.message_content .message_text .set_message {
  display: inline-block;
  width: 48%;
  float: left;
  margin: 10px 0px 10px 10px;
}
.message_content .message_text .set_message p {
  margin-bottom: 5px;
}
.message_content .message_text .set_message i {
  color: red;
}
.message_content .message_text input {
  width: 93%;
  height: 36px;
  background: #fff;
  border: 1px solid #d4d4d4;
  color: #5a5a5a;
  font-size: 16px;
  line-height: 16px;
  outline: none;
  border-radius: 2px;
  padding-left: 10px;
}
.message_content .message_textarea {
  width: 100%;
  padding-left: 15px;
}
.message_content .message_textarea textarea {
  width: 95%;
  padding: 10px;
  background: #fff;
  border: 1px solid #d4d4d4;
  color: #5a5a5a;
  font-size: 14px;
  line-height: 16px;
  outline: none;
  border-radius: 2px;
  font-size: 16px;
}
.message_content .submit_btn {
  width: 94%;
  margin-left: 15px;
  text-align: center;
  border-radius: 2px;
  color: #fff;
  background: #305dc3;

  line-height: 40px;
  font-size: 16px;
  cursor: pointer;
}
.message_content .submit_btn:hover {
  background: #6490e8;
}
</style>




