<template>
  <div class="footer">
    <div class="footerTop">
      <!-- 自适应：0~768 logo -->
      <el-row class="hidden-sm-and-up" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="3">
          <span class="logo">
            <img :style="{height:'45px',width:'173px;'}" :src="style.src_logo">
          </span>
        </el-col>
      </el-row>
      <!-- 自适应：0~768 联系 -->
      <el-row class="hidden-sm-and-up" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="4">
          <span class="connection">
            <img :style="{height:'22px',width:'22px;'}" :src="img1" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img2" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img3" alt>
          </span>
        </el-col>
      </el-row>
      <!-- 自适应：0~768 info -->
      <el-row class="hidden-sm-and-up" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="21">
          <span class="info">
            <a>
              <span>{{$t("Footer.copyright[0]")}}</span>
              <span>{{$t("Footer.copyright[1]")}}</span>
            </a>
          </span>
        </el-col>
      </el-row>
      <!-- 自适应：768~1200 connection + logo  -->
      <el-row class="hidden-xs-only hidden-lg-and-up" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="11">
          <span class="logo">
            <img :style="{height:'45px',width:'173px;'}" :src="style.src_logo">
          </span>
        </el-col>
        <el-col :span="10">
          <span class="connection">
            <img :style="{height:'22px',width:'22px;'}" :src="img1" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img2" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img3" alt>
          </span>
        </el-col>
      </el-row>

      <!-- 自适应：768~1200 info -->
      <el-row class="hidden-xs-only hidden-lg-and-up" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="21">
          <span class="info">
            <a>
              <span>{{$t("Footer.copyright[0]")}}</span>
              <span>{{$t("Footer.copyright[1]")}}</span>
            </a>
          </span>
        </el-col>
      </el-row>

      <!-- 自适应：1200~max logo + connection + info -->
      <el-row class="hidden-md-and-down" :gutter="0">
        <el-col :span="3">
          <p>&nbsp;</p>
        </el-col>
        <el-col class="lg_info" :span="8">
          <span class="info">
            <a>
              <span>{{$t("Footer.copyright[0]")}}</span>
              <span>{{$t("Footer.copyright[1]")}}</span>
            </a>
          </span>
        </el-col>
        <el-col :span="4">
          <p>&nbsp;</p>
        </el-col>
        <el-col :span="9">
          <span class="logo">
            <img :style="{height:'45px',width:'173px;'}" :src="style.src_logo">
          </span>
          <span>&nbsp;&nbsp;&nbsp;&nbsp;</span>
          <span class="connection">
            <img :style="{height:'22px',width:'22px;'}" :src="img1" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img2" alt>
            <img :style="{height:'22px',width:'22px;'}" :src="img3" alt>
          </span>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import "@/styles/footer.css";

export default {
  name: "Footer",
  components: {},
  data() {
    return {
      img1: require("@/assets/weibo.png"),
      img2: require("@/assets/weixin.png"),
      img3: require("@/assets/douyin.png"),
      style: {
        // src_logo: require("@/assets/LOGO_wihte.png")
        src_logo: require("@/assets/maid/logo2.png")

      }
    };
  },
  mounted() {},
  methods: {}
};
</script>

<style scoped>
.footer a {
  text-decoration-line: none;
  color: #d4d4d4;
}
.footer {
  background-color: #2461A2;
  width: 100%;
}
.footer .footerTop {

}
.footer .logo > img {

}

.footer .ft_1 {
}
.footer .connection {
  white-space: nowrap;
}

.footer .connection > img {
  width: 33px;
  height: 33px !important;
  margin: 0px 15px;
}

.footer .connection:nth-child(n) {

}

.footer .our {
  color: #d4d4d4;
}
.footer .return {
}
.footer .info {
}
.footer .lg_info{
  margin: 10px 0px;
}
.footer .info {
  color: #d4d4d4;
}

.footer .footerBottom {
}
.footer .footerBottom a {
  color: #d4d4d4;
}
.footer .footerBottom a:hover {
  color: #ffffff;
  cursor: hand;
}

.footer .element.style{
  font-size: 20px !important;
}
</style>

<style>
</style>



