<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import MessageBoard from "@/views/messageboard/MessageBoard";
import { mapGetters } from "vuex";

import MaidDetail from "@/components/MaidDetail";

export default {
  name: "App", //原
  // name: 'app' //改
  components: {
    "wt-header": Header,
    "wt-footer": Footer,
    "message-board": MessageBoard,
    "maid-detail": MaidDetail
  },
  computed: {
    ...mapGetters(["currentLang", "module", "infoIsShow", "informationMag"])
    // ...mapGetters(["currentLang", "module"])
  },
  data() {
    return {
      isShow: false,
      informationMag: null, //菲佣信息
      infoIsShow: false, //是否显示菲佣详细信息
      tempMSG: {
        ID: "1",
        MID: "A",
        Name: "孙佳灵",
        Introduction:
