        Stature: "160cm",
        Weight: "45kg",
        Birthdate: "1990-08-09",
        Sex: "女",
        Age: "30",
        MaritalStatus: "已婚",
        WorkExp: "3年",
        Nationality: "菲律宾",
        OverseasWorkplace: ["香港", "印尼"],
        Religion: "天主教",
        SalaryRange: "5000-6000",
        Education: "本科",
        avatar: [require("@/assets/productDetails/20170224155340_8447.png")],
        Language: [
          {
            name: "普通话",
            status: "中等"
          },
          {
