            status: "优秀"
          }
        ],
        Skills: [
          {
            title: "烹饪",
            content: {
              c1: {
                name: "中国菜",
                status: 3
              },
              c2: {
                name: "西式",
                status: 4
              }
            }
          },
          {
            title: "家庭整理",
            content: {
              c1: {
                name: "花园管理",
                status: true
              },
              c4: {
                name: "游泳",
                status: true
              },
              c5: {
                name: "作业辅导",
                status: true
              }
            }
          },
          {
            title: "家庭成员护理",
            content: {
              c1: {
                name: "新生儿（0-1岁）",
                status: "1-2年"
              },
              c2: {
                name: "学步儿童（1-3岁）",
                status: "6-12个月"
              },
              c7: {
                name: "花园管理",
                status: "大于2年"
              },
              c8: {
                name: "宠物",
                status: "6-12个月"
              }
            }
          }
        ],
        TheMaidStar: 4,
        IfCollect: true,
        Checked: true
      }
    };
  },
  updated() {},
  methods: {
    ShowDetail() {
      console.log("~~~~ShowDetail");
      let vm = this;
      vm.$store
        .dispatch("update_infoIsShow", 1, true)
        .then(function(response) {})
        .catch(function(error) {
          console.info(error);
        });
    },
    // getInfoMag(id, isShow) {
    // getInfoMag(id, isShow) {
    //   // this.infoIsShow = isShow;
    //   this.infoIsShow = !this.infoIsShow;
    //   let infoAll = this.module;
    //   let filterResult = infoAll.filter(function(item, index, array) {
    //     return item.ID == id;
    //   });
    //   this.informationMag = filterResult;

    //   //console.log(this.informationMag)
    // },
    showInfo() {
      console.log(0);
      this.infoIsShow = !this.infoIsShow;
    },
    getInfoMag(id) {
      this.infoIsShow = !this.infoIsShow;
      let infoAll = this.module;
      let filterResult = infoAll.filter(function(item, index, array) {
        return item.ID == id;
      });
      this.informationMag = filterResult;
      //console.log(this.informationMag)
    },
    showBoard() {
      this.isShow = !this.isShow;
    },
    scrollTop() {
      (function smoothscroll() {
        var currentScroll =
          document.documentElement.scrollTop || document.body.scrollTop;
        if (currentScroll > 0) {
          window.requestAnimationFrame(smoothscroll);
          window.scrollTo(0, currentScroll - currentScroll / 5);
        }
      })();
    }
  }
};
</script>

