  z-index: 2500 !important;
}
/* 右侧微信模块开始 */
.message_fixed .contact_module {
  padding: 20px 8px;
  background: #eeeeee;
}
.message_fixed .contact_module .contact_weixin i {
  display: inline-block;
  width: 30px;
  height: 30px;
  cursor: pointer;
}
.message_fixed .contact_module .contact_weixin i img {
  width: 100%;
}
.message_fixed .contact_module .contact_online i {
  font-size: 30px;
  color: #9b9b9b;
  margin-top: 10px;
  cursor: pointer;
}

