const getters = {
    pageId: state => state.app.pageId,
    usageScenarioTable: state => state.app.usageScenarioTable,
    searchQuery: state => state.app.searchQuery,
    dataSearch: state => state.app.dataSearch,
    pageId_en: state => state.app_en.pageId,
    usageScenarioTable_en: state => state.app.usageScenarioTable,
    searchQuery_en: state => state.app_en.searchQuery,
    dataSearch_en: state => state.app_en.dataSearch,
    addRouters: state => state.settings.addRouters,
    messages: state => state.main.messages,
    currentLang: state => state.main.currentLang,
    currentProduct: state => state.main.currentProduct,
    currentProduct2: state => state.main.currentProduct2,
    products: state => state.main.products,
    machine: state => state.main.machine,
    machine_zh: state => state.main.machine_zh, 
    machine_en: state => state.main.machine_en, 
    dataDrywasher: state => state.main.dataDrywasher,
    dataWashing: state => state.main.dataWashing,
    dataElevator: state => state.main.dataElevator,
    dataPrinter: state => state.main.dataPrinter,
    dataCar: state => state.main.dataCar,
    dataFitness: state => state.main.dataFitness,
    dataAgriculture: state => state.main.dataAgriculture,
    dataForestry: state => state.main.dataForestry,

    // new
    module: state => state.main.module,
    module_zh: state => state.main.module_zh,
    module_en: state => state.main.module_en, 
    module_all: state => state.main.module_all,
    module_all_zh: state => state.main.module_all_zh,
    module_all_en: state => state.main.module_all_en, 
    infoIsShow: state => state.main.infoIsShow, 
    informationMag: state => state.main.informationMag, 
}
export default getters



