import Vue from "vue";

const app = {
    state: {
        pageId: 0, // pageID - 0 默认首页 之后的按banner顺序递增
        searchQuery: null,
        dataSearch: []
    },
    mutations: {

    },
    actions: {
        // 设置页面id
        set_pageId({
            commit,
            state
        }, pageId) {
            return new Promise((resolve, reject) => {
                state.pageId = pageId
                resolve("set_pageId - SUCCESS")
            })
        },
        // 获取页面id
        get_pageId({
            commit,
            state
        }) {
            return new Promise((resolve, reject) => {
                resolve(state.pageId)
            })
        },
 
        // 搜索数据 - searchQuery
        set_searchQuery({
            commit,
            state
        }, searchQuery) {
            return new Promise((resolve, reject) => {
                state.searchQuery = searchQuery
                resolve("set_searchQuery - SUCCESS")
            })
        },
        // dataSearch
        set_dataSearch({
            commit,
            state
        }, dataSearch) {
            return new Promise((resolve, reject) => {
                state.dataSearch = dataSearch
                resolve("set_dataSearch - SUCCESS")
            })
        }
    }
}

export default app



