// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

Vue.config.productionTip = false

import ElementUI from 'element-ui';
Vue.use(ElementUI);
import 'element-ui/lib/theme-chalk/index.css';   // elementUI-主css
import 'element-ui/lib/theme-chalk/display.css'; // elementUI-基于断点的隐藏类

import axios from 'axios'
Vue.prototype.$http = axios;

import store from '@/store'

import $ from 'jquery'
Vue.prototype.$jquery = $

import '@/settings'

// 语言切换
import VueI18n from 'vue-i18n'
Vue.use(VueI18n)
// 引入语言信息
import zh from "@/lang/zh"   //中文
import en from "@/lang/en"   //英文
// 语言设置 - 默认中文 
let str_lang = "zh"
const messages = {
  zh,
  en
}
let data = {
  messages: messages,
  str_lang: str_lang
}
store
  .dispatch("set_lang", data)
  .then(function (response) {
    store
      .dispatch("update_machine", str_lang)
      .then(function (response) { })
      .catch(function (error) {
        console.info(error);
      });
   })
  .catch(function (error) {
    console.info(error);
  });


const main = {
  messages: messages
}

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  // messages,
  components: { App },
  template: '<App/>',
})

export default main



