<template>
  <div v-if="infoIsShow" class="main_info_mag">
    <!-- 关闭图标 -->
    <div class="close_icon">
      <i class="el-icon-circle-close" @click="hiddenInfo()"></i>
    </div>
    <!-- 个人信息开始 -->
    <el-row class="padding_row">
      <el-col :span="4" :lg="7" :md="10">
        <img :src="infoMag[0].avatar" class="avatar_img">
      </el-col>
      <el-col :span="18" :lg="16" :md="14" class="detailed_info">
        <el-row>
          <el-col :span="6" :lg="8" :md="12" class="detailed_info_name">{{infoMag[0].Name}}</el-col>
          <el-col :span="6" :lg="8" :md="12">ID:{{infoMag[0].ID}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" :lg="8" :md="12">性别：{{infoMag[0].Sex}}</el-col>
          <el-col :span="6" :lg="8" :md="12">年龄：{{infoMag[0].Age}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" :lg="8" :md="12">宗教：{{infoMag[0].Religion}}</el-col>
          <el-col :span="6" :lg="8" :md="12">婚否：{{infoMag[0].MaritalStatus}}</el-col>
        </el-row>
        <el-row>
          <el-col :span="6" :lg="8" :md="12">国籍：{{infoMag[0].Nationality}}</el-col>
          <el-col :span="6" :lg="8" :md="12">经验：{{infoMag[0].WorkExp}}</el-col>
        </el-row>
        <el-row>出生日期：{{infoMag[0].Birthdate}}</el-row>
        <el-row>
          <div>
            <span>海外工作经验：</span>
            <span
              class="overseas_work_place"
              v-for="(item,index) in infoMag[0].OverseasWorkplace"
              :key="index + 'a1'"
            >{{item}}</span>
          </div>
        </el-row>
      </el-col>
    </el-row>
    <!-- 个人信息结束 -->

    <!-- 技能掌握开始 -->
    <el-row class="padding_row background_row">
      <el-col :span="14" :lg="14" :md="14">
        <el-col class="skil_name">技能掌握</el-col>
        <el-col v-for="(skill_item,index2) in infoMag[0].Skills" :key="index2 + 'a2'">
          <span v-if="skill_item.title=='家庭成员护理'">
            <el-col
              class="skill_item_name"
              :span="12"
              :lg="12"
              :md="24"
              v-for="(training_item,index3) in skill_item.content"
              :key="index3 + 'a3'"
            >{{training_item.name}}：{{training_item.status}}</el-col>
          </span>
        </el-col>
      </el-col>

      <!-- 烹饪开始 -->
      <el-col :span="10" :lg="10" :md="10">
        <el-col class="skil_name">烹饪</el-col>
        <el-col v-for="(skill_item2,index4) in infoMag[0].Skills" :key="index4 + 'a4'">
          <span v-if="skill_item2.title=='烹饪'">
            <el-col
              class="skill_item_name"
              :span="12"
              :lg="12"
              :md="24"
              v-for="(training_item2,index4) in skill_item2.content"
              :key="index4 + 'a4'"
            >
              {{training_item2.name}}&nbsp;
              <span v-if="training_item2.status==1">
                <i class="el-icon-star-on"></i>
              </span>
              <span v-if="training_item2.status==2">
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
              </span>
              <span v-if="training_item2.status==3">
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
              </span>
              <span v-if="training_item2.status==4">
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
              </span>
              <span v-if="training_item2.status==5">
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
                <i class="el-icon-star-on"></i>
              </span>
            </el-col>
          </span>
        </el-col>
      </el-col>
      <!-- 烹饪结束 -->
    </el-row>
    <!-- 技能掌握结束 -->

    <!-- 语言开始 -->
    <el-row class="padding_row">
      <el-col class="skil_name">语言</el-col>
      <el-col
        class="skill_item_name"
        :span="6"
        v-for="(language_item,index6) in infoMag[0].Language"
        :key="index6+'a6'"
      >{{language_item.name}}：{{language_item.status}}</el-col>
    </el-row>
    <!-- 语言结束 -->

    <!-- 个人简介开始 -->
    <el-row class="padding_row background_row">
      <el-col class="skil_name">个人简介</el-col>
      <el-col class="skill_item_name">{{infoMag[0].Introduction}}</el-col>
    </el-row>
    <!-- 个人简介结束 -->

    <!-- 按钮开始 -->
    <el-row class="padding_row last_padding_row">
      <el-col :span="6">&nbsp;</el-col>
      <el-col :span="12">
        <el-col :span="8">
          <el-button size="medium" type="primary" class="matching_btn">预约</el-button>
        </el-col>
        <el-col :span="8">&nbsp;</el-col>
        <el-col :span="8">
          <el-button size="medium" class="reset_btn">取消</el-button>
        </el-col>
      </el-col>
      <el-col :span="6">&nbsp;</el-col>
    </el-row>
    <!-- 按钮结束 -->
  </div>
</template>
<script>
import { mapGetters } from "vuex";

export default {
  name: "MaidDetail",
  props: ["infoMag"],
  computed: {
    ...mapGetters(["module"]),
  },
  data() {
    return {
      infoIsShow: true
    }
  },
  mounted() {

  },
  methods: {
    hiddenInfo() {
      console.log("~~~~~")
      this.$emit("controlShowInfo")
    }
  }


}
</script>
<style scoped>
.main_info_mag {
  width: 60%;
  margin: 0 auto;
  background: #fff;
  position: relative;
}
.maid_detail .el-row.padding_row {
  padding: 40px 55px;
}
.maid_detail .el-row.last_padding_row {
  padding: 70px 55px;
}
.maid_detail .el-row.background_row {
  background: #fafafa;
}
.maid_detail .el-row .avatar_img {
  width: 80%;
  height: 300px;
  border: 1px solid #f0f0f0;
}
.overseas_work_place {
  margin-right: 10px;
}
.maid_detail .detailed_info .el-row {
  line-height: 50px;
  font-size: 16px;
  color: #747474;
}
.maid_detail .detailed_info .detailed_info_name {
  font-size: 20px;
  color: #333;
}
.maid_detail .close_icon {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 30px;
  color: #3000fd;
}
.maid_detail .skil_name {
  font-size: 18px;
  margin-bottom: 10px;
}
.maid_detail .skill_item_name {
  line-height: 40px;
  font-size: 16px;
  color: #747474;
}
.maid_detail .el-icon-star-on {
  color: #ffc618;
}
.maid_detail .padding_row .matching_btn,
.maid_detail .padding_row .reset_btn {
  width: 100%;
}
</style>





