import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

// import SlideShow from '@/views/slideshow/SlideShow'

import FilipinoMaid from '@/views/filipinomaid/FilipinoMaid'


export const constantRouterMap = [
  { path: '/404', component: () => import('@/views/404'), hidden: true },

  {
    path: '/',
    component: FilipinoMaid,
    redirect: '/',
    name: 'FilipinoMaid',
    hidden: true,
    children: [{
      path: '/',
      component: () => import('@/views/filipinomaid/FilipinoMaid')
    }],
    meta: {
      title: '首页',
      requiresAuth: false,
      roleId: 1
    }
  }
]

export default new Router({
  mode: 'history', //后端支持可开
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

export const asyncRouterMap = [
  {
    path: '/aboutus',
    name: 'AboutUs',
    component: () => import( /* webpackChunkName: "AboutUs" */ '@/views/aboutus/AboutUs'),
    meta: {
      title: '关于我们',
      requiresAuth: false,
      roleId: 1
    }
  },
  {
    path: '/tradeshow',
    name: 'TradeShow',
    component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/TradeShow'),
    meta: {
      title: '行业应用',
      requiresAuth: false,
      roleId: 1
    },
    redirect: '/dryer',

    children: [
      {
        path: '/dryer',
        name: 'DryWasher',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/DryWasher'),
      },
      {
        path: '/washing',
        name: 'Washing',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Washing'),
      },
      {
        path: '/elevator',
        name: 'Elevator',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Elevator'),
      },
      {
        path: '/printer',
        name: 'Printer',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Printer'),
      },
      {
        path: '/car',
        name: 'Car',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Car'),
      },
      {
        path: '/fitness',
        name: 'Fitness',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Fitness'),
      },
      {
        path: "/agriculture",
        name: 'Agriculture',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/tradeshow/Agriculture'),
      }, 
      {
        path: "/forestry",
        name: 'Forestry',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/tradeshow/Forestry'),
      },
      {
        path: "/other",
        name: 'Other',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Other'),
      },
      {
        path: '/details1',
        name: 'Details1',
        component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/TradeShow/Details/Details1'),
      }
    ]
  },
  {
    path: '/productmodel',
    name: 'ProductModel',
    component: () =>
      import(
          /* webpackChunkName: "productmodel" */ '@/views/productmodel/ProductModel'
      ),
    meta: {
      title: '产品',
      requiresAuth: false,
      roleId: 1
    }
  },
  {
    path: '/searchmaid',
    name: 'SearchMaid',
    component: () => import( /* webpackChunkName: "SearchMaid" */ '@/views/searchmaid/SearchMaid'),
    meta: {
      title: '搜寻菲佣',
      requiresAuth: false,
      roleId: 1
    }
  },
  {
    path: '/searchresult',
    name: 'SearchResult',
    component: () => import( /* webpackChunkName: "SearchMaid" */ '@/views/searchmaid/SearchResult'),
    meta: {
      title: '搜索结果',
      requiresAuth: false,
      roleId: 1
    }
  },
  {
    path: '/starmaid',
    name: 'StarMaid',
    component: () => import( /* webpackChunkName: "StarMaid" */ '@/views/starmaid/StarMaid'),
    meta: {
      title: '星标菲佣',
      requiresAuth: false,
      roleId: 1
    }
  },
  
  {
    path: '/employmaid',
    name: 'EmployMaid',
    component: () => import( /* webpackChunkName: "Technical" */ '@/views/employmaid/EmployMaid'),
    meta: {
      title: '雇佣菲佣',
      requiresAuth: false,
      roleId: 1
    }
  },
  // {
  //   path: '/searchproduct',
  //   name: 'SearchProduct',
  //   component: () => import( /* webpackChunkName: "AboutUs" */ '@/views/searchproduct/SearchProduct'),
  //   meta: {
  //     title: '产品查询',
  //     requiresAuth: false,
  //     roleId: 1
  //   },
  //   redirect: '/search',
  //   children: [
  //     {
  //       path: '/search',
  //       name: 'Search',
  //       component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/searchproduct/Search'),
  //     },
  //   ]
  // },
  {
    path: '/detailsintroduce',
    name: 'DetailsIntroduce',
    component: () => import( /* webpackChunkName: "TradeShow" */ '@/views/productmodel/components/DetailsIntroduce'),
    meta: {
      title: '产品细节',
      requiresAuth: false,
      roleId: 1
    }
  },
  { path: '*', redirect: '/404', hidden: true }

  // ]

  // })
]




